
// * * * * THE HOME PAGE
.intro{
  background: url(../assets/bookBackgroundWhite.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 60vh;
  border-radius: 15px;
}

.intro h2, .intro p{
  width: 40%;
  text-align: center;
  margin-left: 5%;
}

.intro h2{
  padding: 3%;
  padding-bottom: 0;
}

.intro p{
  padding: 3%;
  background: $secondary;
  border: 1px solid darken($color: $secondary, $amount: 8);
  border-radius: 15px;
  font-family: $bodyFont;
}

// * * * * THE SEARCH PAGE
main{
  height: 100%;
}

form,
input {
  border: 1px solid $primary;
}

form,
.bookSearch .book button {
  display: flex;
}

input,
button,
.book {
  border-radius: 0.3rem;
}

input,
.bookSearch .book h2,
.bookInfo {
  width: 100%;
}

input,
button,
.bookSearch .book h2 {
  font-size: 2rem;
}

main {
  margin: 5%;
}

form {
  border-radius: 1.5rem;
  background: $pageBlock;
  padding: 2rem;
  flex-direction: column;
}

h2,
h3 {
  font-size: clamp(2.5rem, 3vh, 4rem);
  font-family: $headingFont;
  margin: 1% 0;
  padding: 0;
}

p {
  font-size: clamp(1.5rem, 2vh, 2rem);
  line-height: 2.7rem;
  font-family: $bodyFont;
}

input {
  height: 4rem;
  padding-left: 1rem;
}

button {
  border-style: none;
  // background-color: lighten($color: $accent, $amount: 3);
  background: rgb(9,4,70);
background: linear-gradient(0deg, rgba(9,4,70,1) 0%, rgba(99,113,146,1) 74%);
  color: #fff;
  padding: 1rem 3rem;
  margin: 1rem 0;
  border-radius: 5px;
}

.book {
//   background-color: $secondary;
  padding: 1rem;
  margin-top: 4rem;
}

.book h2 {
  margin: 2rem 0;
}

.bookSearch .book {
  .imageContainer {
    width: 100%;
  }
  img {
    display: block;
    width: 100%;
    max-width: 15rem;
  }
  button {
    align-items: center;
  }
}

.bookInfo {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas:
    "info info"
    "blurb blurb";
  p {
    margin: 0;
    padding: 0 1rem;
    font-size: 1.8rem;
  }
  .bookDescription{
      padding: 2rem 0;
  }
}

.header {
  grid-area: header;
}

.blurb {
  grid-area: blurb;
}

.info {
  grid-area: info;
}

// NEEDS STYLING
button.toggledButton {
  background: rgba(99,113,146,1);
}